___CSS_LOADER_EXPORT___.push([module.id, `* {
    font-family: "Roboto", sans-serif;
}

body {
    background-color: #253f5c;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

#content {
    background-color: #ffffff;
    padding: 1rem;
    border-radius: 10px;
}

.searchbar {
    display: flex;
    gap: 1rem;
    justify-content: stretch;
    align-items: center;
}

.searchbar > input {
    border: none;
    font-size: 1.25rem;
}

.searchbar > input:focus {
    border: none;
    outline: none;
}

.searchbar > svg {
    height: 2rem;
}

.data {
    padding: 1rem;
    margin-top: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.data > img {
    height: 8rem;
    margin-bottom: 1.5rem;
}

.data > .temp {
    font-size: 2.5rem;
}

.data > .conditions {
    font-size: 1.25rem;
}

.data > .otherinfo {
    margin-top: 2rem;
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.data > .otherinfo > .precipprob {
    display: flex;
    gap: 0.5rem;
    align-items: center;
}

.data > .otherinfo > .precipprob > img {
    height: 1.5rem;
}

.data > .otherinfo > .precipprob > div {
    font-size: 1.5rem;
}

.data > .otherinfo > .sun {
    display: flex;
    gap: 0.5rem;
    align-items: center;
}

.data > .otherinfo > .sun > img {
    height: 1.5rem;
}

.data > .otherinfo > .sun > div {
    font-size: 1.5rem;
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;IACI,iCAAiC;AACrC;;AAEA;IACI,yBAAyB;IACzB,aAAa;IACb,aAAa;IACb,uBAAuB;IACvB,mBAAmB;AACvB;;AAEA;IACI,yBAAyB;IACzB,aAAa;IACb,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,SAAS;IACT,wBAAwB;IACxB,mBAAmB;AACvB;;AAEA;IACI,YAAY;IACZ,kBAAkB;AACtB;;AAEA;IACI,YAAY;IACZ,aAAa;AACjB;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,gBAAgB;IAChB,aAAa;IACb,sBAAsB;IACtB,mBAAmB;AACvB;;AAEA;IACI,YAAY;IACZ,qBAAqB;AACzB;;AAEA;IACI,iBAAiB;AACrB;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,gBAAgB;IAChB,WAAW;IACX,aAAa;IACb,8BAA8B;AAClC;;AAEA;IACI,aAAa;IACb,WAAW;IACX,mBAAmB;AACvB;;AAEA;IACI,cAAc;AAClB;;AAEA;IACI,iBAAiB;AACrB;;AAEA;IACI,aAAa;IACb,WAAW;IACX,mBAAmB;AACvB;;AAEA;IACI,cAAc;AAClB;;AAEA;IACI,iBAAiB;AACrB","sourcesContent":["* {\n    font-family: \"Roboto\", sans-serif;\n}\n\nbody {\n    background-color: #253f5c;\n    height: 100vh;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n}\n\n#content {\n    background-color: #ffffff;\n    padding: 1rem;\n    border-radius: 10px;\n}\n\n.searchbar {\n    display: flex;\n    gap: 1rem;\n    justify-content: stretch;\n    align-items: center;\n}\n\n.searchbar > input {\n    border: none;\n    font-size: 1.25rem;\n}\n\n.searchbar > input:focus {\n    border: none;\n    outline: none;\n}\n\n.searchbar > svg {\n    height: 2rem;\n}\n\n.data {\n    padding: 1rem;\n    margin-top: 1rem;\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n}\n\n.data > img {\n    height: 8rem;\n    margin-bottom: 1.5rem;\n}\n\n.data > .temp {\n    font-size: 2.5rem;\n}\n\n.data > .conditions {\n    font-size: 1.25rem;\n}\n\n.data > .otherinfo {\n    margin-top: 2rem;\n    width: 100%;\n    display: flex;\n    justify-content: space-between;\n}\n\n.data > .otherinfo > .precipprob {\n    display: flex;\n    gap: 0.5rem;\n    align-items: center;\n}\n\n.data > .otherinfo > .precipprob > img {\n    height: 1.5rem;\n}\n\n.data > .otherinfo > .precipprob > div {